@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@300;400;500;700&display=swap);
:root{--Mobile-Border: black;--Dark: #22252d;--Dark-Blue: #292d38;--White: #f9f9f9;--Blue: #40b3da;--Green: #18d2b4;--Gray: #f0f0f0;--Text: #2c2a2a;--Background-Green: rgba(106, 154, 158, 0.8);--Background: var(--White);--Previous: rgba(44, 42, 4, 0.7);--Button-Container: var(--Gray)}.dark{--Text: var(--White);--Background: var(--Dark);--Previous: rgba(250, 250, 250, 0.7);--Button-Container: var(--Dark-Blue)}.slide-top{-webkit-animation:slide-top .2s linear both;animation:slide-top .2s linear both}@-webkit-keyframes slide-top{0%{-webkit-transform:translateY(100%);transform:translateY(100%)}100%{-webkit-transform:translateY(25%);transform:translateY(25%)}}@keyframes slide-top{0%{-webkit-transform:translateY(100%);transform:translateY(100%)}100%{-webkit-transform:translateY(25%);transform:translateY(25%)}}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none}*,*::before,*::after{box-sizing:border-box;font-family:"Red Hat Text",Gotham Rounded,sans-serif;font-weight:normal}body{margin:0;padding:0;background:#fff;color:#2c2a2a;color:var(--Text);font-family:"Red Hat Text",sans-serif,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.container{width:100%;height:100vh;background:#f9f9f9;background:var(--Background);margin:0 auto;box-shadow:rgba(100,100,111,.5) 0px 7px 29px 0}@media(min-width: 640px){.container{max-width:375px;max-height:812px;border:12px solid black;border:12px solid var(--Mobile-Border);border-radius:36px;position:relative;overflow:hidden;height:calc(100vh - 80px);margin:40px auto}.container::before{content:"";width:40%;height:25px;background-color:black;background-color:var(--Mobile-Border);border-radius:0 0 20px 20px;position:absolute;top:0;left:50%;-webkit-transform:translatex(-50%);transform:translatex(-50%);z-index:1000}.container::after{content:"";width:100px;height:5px;background:black;background:var(--Mobile-Border);border-radius:2px;position:absolute;bottom:6px;left:50%;-webkit-transform:translatex(-50%);transform:translatex(-50%);z-index:1000}}@media(max-width: 375px){.container{font-size:2rem}.container .current{font-size:calc((100vw - 6rem)/7)}}
